import Loader from "react-loader-spinner";import React from "react";export default function CustomLoader() {    return (<div            className="h-screen content-center relative flex items-center justify-center  bg-transparent">            <Loader                type="Circles"                color="#ff8c00"                height={100}                width={100}                timeout={3000}//3 secs                className="z-50 bg-transparent "            />        </div>    )}